import md5 from 'md5'

export default (obj) => {
    let paramsToken = ''
    console.log(obj)
    Object.keys(obj).sort().forEach((item) => {
        paramsToken += (item + ((obj[item] || '').toString()))
    })

    paramsToken += '048a9c4943398714b356a696503d2d36'

    return md5(paramsToken)
}



