import axios from 'axios'
import qs from 'qs'
import { Notification } from 'element-ui'

// multipart/form-data
let formUrl = [
    '/product/product/upload-obj-file'
]

// application/json
let specialUrl = [
    '/poundage/activity/create',
    '/poundage/activity/edit'
]

function isSpecialUrl(url) {
    let tag = false
    specialUrl.map(item => {
        if (url === item) {
            tag = true
        }
    })
    return tag
}

function isFormlUrl(url) {
    let tag = false
    formUrl.map(item => {
        if (url === item) {
            tag = true
        }
    })
    return tag
}
// 最大超时10s
axios.defaults.timeout = 60000
// mock开关
// let mockUrl = '//10.134.130.229:7300/mock/5c8ef8ebabdb3066e64c8932'
// if (mockUrl) {
//     axios.defaults.baseURL = mockUrl
// }
// 统一 post 请求数据格式
axios.interceptors.request.use((config) => {
    if (config.method === 'post') {
        if (isFormlUrl(config.url)) {
            config.headers['Content-Type'] = 'multipart/form-data'
        } else if (isSpecialUrl(config.url)) {
            config.headers['Content-Type'] = 'application/json;charset=UTF-8'
        } else {
            config.data = qs.stringify(config.data)
            config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
        }
    }
    return config
}, (error) => {
    return Promise.reject(error)
})

// GET请求,将url和请求参数传入，通过Promise，将成功和失败的数据派发出去
export function getHttp(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(
            url, {
                params: params
            }
        )
            .then(response => {
                if (response.data.code === 401) {
                    window.location = 'login.html'
                } else {
                    resolve(response.data)
                }
            }, err => {
                let { message } = err
                Notification({
                    type: 'error',
                    message
                })
                reject(err)
            })
    })
}

// POST请求，将url,请求参数和body传入，通过Promise，将成功和失败的数据派发出去
export function postHttp(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(
            url,
            params
        )
            .then(response => {
                resolve(response.data)
            }, err => {
                let { message } = err
                Notification({
                    type: 'error',
                    message
                })
                reject(err)
            })
    })
}



