 * @Date: 2019-03-14 17:04:58
 * @LastEditTime: 2019-04-12 14:56:29
 -->
<template>
    <div>
        <div>
            <el-button type = 'primary' @click = 'handleAddQuestionType'>添加问题分类</el-button>
        </div>

            <el-table
                :data = 'data'
                border
                stripe
                class = '__table__'
                row-key="id"
                height="1000"
                v-loading = 'loading'
            >
                <el-table-column label = '问题ID' align = 'center' prop = 'categoryId' width = '120'></el-table-column>
                <el-table-column label = '分类标题' align = 'center' prop = 'categoryName' width = '160'></el-table-column>
                <el-table-column label = '客服分组' align = 'center' prop = 'agentGroup.name' width = '160'></el-table-column>
                <el-table-column label = '是否显示' align = 'center' width = '120'>
                    <template slot-scope="scope">
                    <p>{{ scope.row.display === 1 ? '显示': '不显示' }}</p>
                    </template>
                </el-table-column>
                <el-table-column label = '添加时间' align = 'center' prop = 'createdAt' width = '180'></el-table-column>
                <el-table-column label = '修改时间' align = 'center' prop = 'modifiedAt' width = '180'></el-table-column>
                <el-table-column label = '操作' align = 'center' width = '180' fixed="right">
                    <template slot-scope = 'scope'>
                        <div>
                            <el-button size = 'mini' type = 'text' @click = 'handleEdit(scope.row)'>编辑</el-button>
                            <el-button size = 'mini' type = 'text' @click = 'handleDel(scope.row)'>删除</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>

        <!-- <el-pagination
            background
            layout="prev, pager, next"
            :total="dataCount || 1"
            :current-page.sync = 'currentPage'
            @current-change = 'handleTogglePage'
            v-show="!loading"
            :page-size="20"
            class = 'pagination'
        ></el-pagination> -->
        <template v-if = 'isOpenDialog'>
            <QuestionSortModal
                @close = 'closeDialog'
                @update = 'fetchList'
                :identity = 'identity'
                :datas = 'currentModalData'
                 />
        </template>
    </div>
</template>
<script>
    import API from '../api/index.js'
    import QuestionSortModal from './components/QuestionSortModal.vue'

    export default {
        props: {
            identity: {
                type: Number,
                default: 1
            }
        },
        components: {

            QuestionSortModal
        },
        data () {
            return {
                data: [],
                // 当前弹层的数据
                currentModalData: {},
                // dataCount: 1,
                currentPage: 1,
                loading: false,
                isOpenDialog: false
            }
        },
        mounted () {
            this.fetchList()
        },
        watch: {
            '$route': 'fetchList'
        },
        methods: {
            closeDialog () {
                this.isOpenDialog = false
                this.currentModalData = {}
            },
            fetchList (page = 1) {
                this.loading = true
                this.__DUHTTP__({
                    actionText: '获取列表',
                    service: () => API.CustomerService.categeroyList({
                        faqGroup: this.identity // 1 买家 2 卖家
                    }),
                    successCallback: (res) => {
                        if (!res) return
                        let { data: { list } } = res
                        this.data = list
                    },
                    finallyCallback: () => { this.loading = false }
                })
            },
            handleAddQuestionType () {
                this.isOpenDialog = true
            },
            // handleTogglePage(page) {
            //     this.fetchList(page)
            // },
            handleDraggableEnd (target) {
                let { newIndex, oldIndex } = target
                let dragIds
                if (newIndex === oldIndex) return
                // 判断方向
                if (newIndex > oldIndex) {
                    // 向下
                    console.log('向下')
                    dragIds = this.data.filter((item, index) => {
                        if (index === newIndex || index === newIndex - 1) return item
                    }).map(item => item.categoryId)
                } else {
                    // 向上
                    console.log('向上')
                    dragIds = this.data.filter((item, index) => {
                        if (index === newIndex || index === newIndex + 1) return item
                    }).map(item => item.categoryId)
                }

                console.log(dragIds)
                this.__DUHTTP__({
                    actionText: '更新排序',
                    service: () => API.CustomerService.categoryReordered({
                        categoryId: dragIds
                    })
                })
            },
            handleEdit (row) {
                this.currentModalData = row
                this.isOpenDialog = true
            },
            handleDel (row) {
                this.__DUHTTP__({
                    actionText: '删除问题分类',
                    service: () => API.CustomerService.categoryDelete({
                        categoryId: +row.categoryId
                    }),
                    confirmModal: {
                        title: '警告',
                        text: '确定删除这条数据吗？'
                    },
                    showTips: true,
                    successCallback: (res) => {
                        if (!res) return
                        this.fetchList()
                    }
                })
            }
        }
    }
</script>
<style scoped>
.__table__ {
    margin: 20px 0;
}
</style>



