import CustomerService from './customerService'
const jsContexts = require.context('.', true, /[^index]\.js$/)

let api = {}

jsContexts.keys().forEach(jsContext => {
  let fileContext = jsContexts(jsContext)
  let sourceName = jsContext.replace(/(\.\/|\.js)/g, '')
  let firstUpName = sourceName[0].toUpperCase() + sourceName.slice(1)
  api[firstUpName] = fileContext.default
})

console.log(api)

export default {
  CustomerService
}



