<template>
    <el-dialog
        :title="(Object.keys(datas).length ? '编辑': '添加') + '问题分类'"
        width="600px"
        append-to-body
        center
        visible
        @close = '$emit("close")'
        >
        <el-form :model= 'form' :rules="rules" label-width="110px" ref= 'form'>
            <el-form-item label ='分类标题' prop = 'title'>
                <el-input placeholder="请输入标题" v-model='form.title' clearable style='width: 194px'></el-input>
            </el-form-item>
            <el-form-item label ='客服分组' prop = 'groupName'>
                <el-select v-model = 'form.groupName' placeholder="请选择" clearable>
                    <el-option
                        v-for="item in enumGroups"
                        :key="item.groupId"
                        :label="item.groupName"
                        :value="item.groupName"
                        >
                    </el-option>
                </el-select>
            </el-form-item>
            <!-- <el-form-item label ='封面图'>
                <el-upload
                    class="question__avatar__uploader"
                    action= '/common/qiniu/save-image'
                    :on-success="handleUploadSuccess"
                >
                    <template v-if = 'imgList.length'>
                        <div>
                            <img v-for = '(img, index) in imgList' :key = 'index' :src="img" class="avatar">
                        </div>
                    </template>
                    <template v-else>
                        <i class="el-icon-plus question__avatar__uploader--icon"></i>
                    </template>
                </el-upload>
            </el-form-item> -->
            <el-form-item label ='是否在前台显示'>
                <el-radio-group v-model="form.isShowOnFront">
                    <el-radio :label="1">显示</el-radio>
                    <el-radio :label="0">不显示</el-radio>
                </el-radio-group>
            </el-form-item>
        </el-form>
        <div slot="footer">
            <el-button type="primary"  @click = 'handleConfirmAddQuestionType' :loading="loading">提交保存</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import API from '../../api/index.js'

    export default {
        props: {
            datas: {
                type: Object,
                default: () => {}
            },
            // 1 买家 2 卖家
            identity: {
                type: Number,
                default: 1
            }
        },
        data () {
            return {
                form: {
                    title: '',
                    groupId: '',
                    groupName: '',
                    isShowOnFront: 1
                },
                rules: {
                    title: {
                        required: true, message: '标题不能为空！'
                    },
                    groupName: {
                        required: true, message: '客服分组不能为空！'
                    }
                },
                enumGroups: [],
                imgList: [],
                loading: false
            }
        },
        mounted () {
            this.fetchQiyu()
            this.setInitFormData()
        },
        methods: {
            handleUploadSuccess (response, file, fileList) {
                console.log(response, file, fileList)
            },
            handleConfirmAddQuestionType () {
                this.$refs.form.validate(status => {
                    if (!status) return console.log('失败')
                    this.fetchAddQuestionType()
                })
            },
            fetchAddQuestionType () {
                console.log(this.form)
                let { groupName } = this.form
                let groupId = this.filterGroupId()
                let params = {
                    categoryId: this.datas.categoryId || 0,
                    categoryName: this.form.title,
                    faqGroup: this.identity,
                    display: this.form.isShowOnFront,
                    agentGroup: {
                        id: groupId,
                        name: groupName
                    }
                }
                this.loading = true
                // debugger
                this.__DUHTTP__({
                    actionText: '操作',
                    service: () => API.CustomerService.categeroy(params),
                    showTips: true,
                    successCallback: (res) => {
                        if (!res) return
                        console.log('======', res)
                        this.$emit('update')
                        this.$emit('close')
                    },
                    finallyCallback: () => { this.loading = false }
                })
            },
            fetchQiyu () {
                this.__DUHTTP__({
                    actionText: '获取客服分组列表',
                    service: () => API.CustomerService.qiyu({
                        faqGroup: this.identity
                    }),
                    successCallback: (res) => {
                        if (!res) return
                        let { data: { list } } = res
                        this.enumGroups = [].concat(list)
                    }
                })
            },
            setInitFormData () {
                if (Object.keys(this.datas).length === 0) return
                let { agentGroup: { id, name }, categoryName, display } = this.datas
                this.form.groupId = id
                this.form.groupName = name
                this.form.title = categoryName
                this.form.isShowOnFront = display
            },
            filterGroupId () {
                let target = this.enumGroups.filter(item => item.groupName === this.form.groupName)
                if (target) {
                    return target[0].groupId
                }
            }
        }
    }
</script>
<style>
.question__avatar__uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.question__avatar__uploader .el-upload:hover {
    border-color: #409EFF;
}
.question__avatar__uploader--icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}
</style>



